<!-- components/RetirementCalculator.vue -->
<template>
  <section class="py-16 bg-gray-50">
    <div class="container mx-auto px-4 max-w-5xl">
      <h2 class="text-3xl font-bold text-gray-900 mb-4">
        Retirement Calculator
      </h2>
      <p class="text-gray-600 mb-8">
        Plan for your future by calculating how much you need to save for a comfortable retirement.
      </p>

      <div class="lg:flex lg:gap-8">
        <!-- Calculator Form -->
        <form @submit.prevent="calculate" class="bg-white shadow-lg rounded-xl p-8 space-y-4 lg:flex-1">
          <div>
            <label for="currentAge" class="block text-sm font-medium text-gray-700">Current Age</label>
            <input type="number" id="currentAge" v-model.number="currentAge" class="w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none" min="0" />
          </div>

          <div>
            <label for="retirementAge" class="block text-sm font-medium text-gray-700">Retirement Age</label>
            <input type="number" id="retirementAge" v-model.number="retirementAge" class="w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none" min="0" />
          </div>

          <div>
            <label for="currentSavings" class="block text-sm font-medium text-gray-700">Current Savings ($)</label>
            <input type="number" id="currentSavings" v-model.number="currentSavings" class="w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none" min="0" />
          </div>

          <div>
            <label for="monthlyContribution" class="block text-sm font-medium text-gray-700">Monthly Contribution ($)</label>
            <input type="number" id="monthlyContribution" v-model.number="monthlyContribution" class="w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none" min="0" />
          </div>

          <div>
            <label for="annualReturn" class="block text-sm font-medium text-gray-700">Expected Annual Return (%)</label>
            <input type="number" id="annualReturn" v-model.number="annualReturn" class="w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none" min="0" step="0.1" />
          </div>

          <div>
            <label for="targetAmount" class="block text-sm font-medium text-gray-700">Target Retirement Savings ($)</label>
            <input type="number" id="targetAmount" v-model.number="targetAmount" class="w-full px-3 py-2 border rounded-lg focus:ring-2 focus:ring-blue-500 focus:outline-none" min="0" />
          </div>

          <button type="submit" class="w-full bg-blue-600 hover:bg-blue-700 text-white font-semibold py-3 px-4 rounded-lg transition">
            Calculate
          </button>
        </form>

        <!-- Breakdown Panel -->
        <div v-if="result" class="lg:flex-1 mt-8 lg:mt-0 bg-white shadow-lg rounded-xl p-6">
          <div class="mb-4 text-center font-medium text-gray-600">Retirement Savings at Age {{ retirementAge }}</div>
          <div class="grid grid-cols-1 md:grid-cols-2 gap-4">
            <div class="bg-gray-100 p-4 rounded-lg text-center">
              <div class="text-gray-600 mb-1">What you'll have</div>
              <p class="text-2xl font-bold text-gray-900">${{ formattedHave }}</p>
            </div>
            <div class="bg-gray-100 p-4 rounded-lg text-center">
              <div class="text-gray-600 mb-1">What you'll need</div>
              <p class="text-2xl font-bold text-gray-900">${{ formattedNeed }}</p>
            </div>
          </div>

          <div v-if="gap !== null" class="mt-4 text-center">
            <p class="text-lg font-medium" :class="gap >= 0 ? 'text-green-600' : 'text-red-600'">
              {{ gap >= 0 ? 'You are on track!' : `You need $${Math.abs(gap).toLocaleString()} more to reach your goal.` }}
            </p>
          </div>

          <!-- <a href="#how-this-works" class="text-blue-600 hover:underline mt-2 inline-block">
            How did we calculate your results?
          </a> -->
          <RetirementBreakdown
            v-if="result"
            :result="result"
            :formattedHave="formattedHave"
            :formattedNeed="formattedNeed"
            :monthlyContribution="monthlyContribution"
            :suggestedContribution="suggestedContribution"
            :ageSavingsRunsOutCurrent="80"
            :ageSavingsRunsOutTarget="95"
          />

        </div>
      </div>
    </div>
  </section>
</template>

<script setup>
import { ref, computed } from 'vue'

const currentAge = ref(30)
const retirementAge = ref(67)
const currentSavings = ref(10000)
const monthlyContribution = ref(500)
const annualReturn = ref(6)
const targetAmount = ref(1500000)

const result = ref(false)
const haveAmount = ref(0)
const needAmount = ref(0)
const gap = ref(null)
const suggestedContribution = ref(0)

const calculate = () => {
  const years = retirementAge.value - currentAge.value
  if (years <= 0) {
    haveAmount.value = 0
    needAmount.value = targetAmount.value
    gap.value = -targetAmount.value
    suggestedContribution.value = 0
    result.value = true
    return
  }

  const months = years * 12
  const r = annualReturn.value / 100 / 12

  // Current plan future value
  const fvCurrent = currentSavings.value * Math.pow(1 + r, months)
  const fvContributions = monthlyContribution.value * ((Math.pow(1 + r, months) - 1) / r)
  haveAmount.value = fvCurrent + fvContributions

  // Target plan
  needAmount.value = targetAmount.value
  gap.value = haveAmount.value - needAmount.value

  // Calculate suggested monthly contribution to reach target
  const fvTargetContrib = targetAmount.value - fvCurrent
  suggestedContribution.value = fvTargetContrib / ((Math.pow(1 + r, months) - 1) / r)

  result.value = true
}

const formattedHave = computed(() =>
  haveAmount.value.toLocaleString(undefined, { maximumFractionDigits: 0 })
)
const formattedNeed = computed(() =>
  needAmount.value.toLocaleString(undefined, { maximumFractionDigits: 0 })
)
</script>

